/*-----------------Header-----------------*/
.header-one .header-mini .pro-header-options .dropdown {
  float: right;
}

.header-one .header-mini .pro-header-options li > a .fas,
.header-one .header-mini .pro-header-options li > a .far,
.header-one .header-mini .pro-header-options li button .fas,
.header-one .header-mini .pro-header-options li button .far {
  color: #ffffff;
  font-size: 2rem;
  transition: 0.4s;
}

.header-one .header-mini .pro-header-options li > a,
.header-one .header-mini .pro-header-options li button {
  position: relative;
  padding: 0;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-one .header-mini .pro-header-options .dropdown {
  float: right;
}

.header-one .header-mini .pro-header-options .cart-left {
  float: left;
  position: relative;
}

.header-one .header-mini .pro-header-options li {
  position: relative;
  display: inline-block;
  list-style: none;
  margin-left: 35px;
}

.header-one
  .header-mini
  .pro-header-options
  .dropdown
  .dropdown-menu
  .shopping-cart-items {
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 15px;
  padding-bottom: 15px;
  max-height: 350px;
  overflow-y: auto;
}

.header-one .header-mini .pro-header-options .dropdown .dropdown-menu {
  -webkit-animation: animationcartmenu ease 0.5s;
  animation: animationcartmenu ease 0.5s;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  box-shadow: 0 0 5px 2px rgb(0 0 0 / 10%);
  width: 270px;
  border-radius: 0;
  border-color: #fff;
  z-index: 99999;
  margin-top: 25px;
  position: absolute;
  left: 1px !important;
}

.header-one
  .header-mini
  .pro-header-options
  .dropdown
  .dropdown-menu
  .shopping-cart-items
  li {
  float: left;
  position: relative;
  width: 100%;
  margin-bottom: 10px;
  border-bottom: 1px solid #dee2e6;
  padding-bottom: 10px;
  margin-left: 0;
  color: black;
}

.header-one .header-mini .pro-header-options li {
  position: relative;
  display: inline-block;
  list-style: none;
  margin-left: 35px;
}

.header-one .header-mini .pro-header-options li > a .badge,
.header-one .header-mini .pro-header-options li button .badge {
  position: absolute;
  right: -4px;
  top: 18px;
  height: 16px;
  min-width: 16px;
  border-radius: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.header-one .header-mini .pro-header-options li .cart-right {
  margin-left: 20px;
  text-align: left;
}

.img-fluid.logo {
  max-width: 100px;
}

/*-----------------Nav-----------------*/
.navbar-expand-lg .navbar-nav .nav-link {
    padding-right: 0.5rem;
    padding-left: 0.5rem;
    color: #ffffff;
    /* background: transparent; */
    font-size: 16px;
}

/*-----------------Slider-----------------*/
.slider-full-height .carousel-inner {
    height: calc(100vh - 150px)!important;
}
.slider-full-height .carousel-inner .carousel-item {
    height: calc(100vh - 150px)!important;
    overflow: hidden;
}

.video-bg {
    width: 100%;
}
